.button {
  font-family: "Manrope", sans-serif;
  font-weight: 600;
  font-size: 14px;
  line-height: 150%;
  text-transform: uppercase;
  color: #121212;
  box-sizing: border-box;
  height: 48px;
  width: 260px;
  text-decoration: none;
  transition: all 0.3s;
  cursor: pointer;

  &__img {
    height: 12px;
    width: 24px;
    text-align: center;
    padding: 0 12px;
    margin: auto;

    @include onDesktop {
      width: 28px;
    }
  }

  &__ticket--arroworange {
    height: 48px;
    background: #ffe339;
    border: 1px solid #ffe339;
    transition: all 1s;

    &:hover {
      opacity: 0.6;
      background-color: #ffec7a;
    }
  }

  &__ticket--blue {
    width: 240px;
    height: 48px;
    margin: 40px 0 0;
    color: #fff;
    background: #03839e;
    border: 1px solid #03839e;
    transition: all 1s;

    &:hover {
      opacity: 0.6;
    }
  }

  &__ticket--white {
    height: 48px;
    width: 240px;
    text-decoration: none;
    transition: all 0.3s;
    cursor: pointer;
    font-family: "Manrope", sans-serif;
    font-weight: 600;
    font-size: 14px;
    line-height: 150%;
    color: #121212;
    background:  #ffe339;
    border: none;

    &:hover {
      background: #ffec7a;
      opacity: 0.6;
    }



    @include onTABS {
      width: 270px;
      background: #fff;
      border: 1px solid #03839e;

      &:hover {
        background: #03839e;
        opacity: 0.6;
      }
    }
  }
}
